<template>
    <div class="contact">
        <div class="contact-title">
            <p class="sub-title">聯絡我們</p>
            <p class="title">CONTACT US</p>
        </div>
        <div class="contact-content">
            <div class="contact-form">
                <div class="contact-aboutus">
                    <div class="contact-content-title">聯絡電話</div>
                    <div class="contact-aboutus-phone">
                        <div class="icon">
                            <img src="~@/assets/img/contact/phone-icon.png" alt="" srcset="">
                        </div>
                        <div class="info">
                            <p>聯絡服務時間<br>周一至周五  8:00am - 5:00pm</p>
                            <p class="number">04-12345678</p>
                        </div>
                    </div>
                    <div class="contact-content-title">公司資訊</div>
                    <div class="contact-aboutus-company">
                        <ul>
                            <li>
                                <img src="~@/assets/img/contact/map-icon.png" alt="" class="icon">
                                <div class="info">
                                    <p class="title">公司位置</p>
                                    40755 台中市西屯區朝富路213號3樓之1
                                </div>
                            </li>
                            <li>
                                <img src="~@/assets/img/contact/mail-icon.png" alt="" class="icon">
                                <div class="info">
                                    <p class="title">郵件地址</p>
                                    info@domain.com
                                </div>
                            </li>
                            <li>
                                <img src="~@/assets/img/contact/office-icon.png" alt="" class="icon">
                                <div class="info">
                                    <p class="title">傳真</p>
                                    04-12345678
                                </div>
                            </li>
                            
                        </ul>
                    </div>
                </div>
                <div class="right">
                    <div class="contact-content-title">通過下面的表格輸入您的意見，我們的客服專員會盡快與您聯繫。</div>
                    <div class="contact-form-box">
                        <el-input v-model="input" placeholder="姓名"></el-input>
                        <el-input v-model="input" placeholder="電話"></el-input>
                        <el-input v-model="input" placeholder="電子信箱"></el-input>
                        <el-select v-model="value" placeholder="問題類型">
                            <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                        <el-input
                            type="textarea"
                            :rows="2"
                            placeholder="请输入内容"
                            :autosize="{ minRows: 10, maxRows: 10}"
                            v-model="textarea">
                        </el-input>

                        <button class="car-btn" @click="open">確認送出</button>
                    </div>
                </div>                    
            </div>
        </div>
        <div class="google-map">
            <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3640.2674423090375!2d120.63348891536776!3d24.162351678830046!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x34693dee204d9c8d%3A0xdf59119ec73969a!2zNDA35Y-w54Gj5Y-w5Lit5biC6KW_5bGv5Y2A5pyd5a-M6LevMjEz6JmfMTHomZ_mqJPkuYsx!5e0!3m2!1szh-TW!2sjp!4v1640104461761!5m2!1szh-TW!2sjp" width="1920" height="380" style="border:0;" allowfullscreen="" loading="lazy"></iframe>
        </div>    
    </div>
</template>

<script>
export default {
    name: 'contact',
    data() {
        return {
            input:'',
            textarea:'',
            value: '',
            options: [{
                value: '选项1',
                label: '車1'
                }, {
                value: '选项2',
                label: '車2'
                },
            ],
        }
    },
    methods: {
        open() {
            this.$alert('感謝您，您的訊息已送出，我們將盡快回復您!', '確認通知', {
            confirmButtonText: '確定',
            callback: action => {
            }
            });
        }
    },


}
</script>

<style lang='scss' scoped>
.contact {
    margin-top: 105px;
    .contact-title {
        background: url('~@/assets/img/contact/contact-banner.png') no-repeat center;
        background-size: cover;
    }
    .contact-link {
        width: 100%;
    }
    .contact-content {
        max-width: 1200px;
        width: 90%;
        margin: auto;
        margin-top: 50px;
        .contact-content-title {
            border-left: 5px #ef6d38 solid;
            padding-left: 15px;
        }
        .contact-form {
            margin-top: 100px;
            display: flex;
            flex-wrap: wrap;
            .right {
                width: 52%;
                min-width: 300px;
            }
            .contact-form-box {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                margin: 0 0 30px 0;
                div {
                    margin: 30px 0 0 0;
                    @media screen and (max-width: 760px) { 
                        width: 100%;
                    }
                }
                .el-input, .el-select{
                    width: 48%;
                    >>> .el-input__inner{
                        min-width: auto !important;
                    }
                }
            }
            .el-textarea {
                .el-textarea__inner {
                    border-radius: 25px;
                    
                }
            }
        }
        .contact-aboutus {
            width: 43%;
            margin-right : 5%;
            min-width: 350px;
            color: #333333;
            .contact-aboutus-phone{
                background: #f5f5f5;
                width: 100%;
                height: 155px;
                margin: 20px 0;
                display: flex;
                .icon {
                    width: 100px;
                    background: #ca582a;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
                .info {
                    line-height: 24px;
                    padding: 30px 20px 0 20px;
                    .number {
                        font-size: 36px;
                        font-weight: bold;
                        color: #ef6d38;
                        margin-top: 15px;
                    }
                }
            }
            .contact-aboutus-company {
                background: #f5f5f5;
                padding: 30px 40px;
                margin: 20px 0;
                ul {
                    li {
                        display: flex;
                        align-items: center;
                        margin-bottom: 30px;
                        .icon {
                            margin-right: 15px;
                            width: 30px;
                        }
                        .info {
                            .title {
                                color: #ef6d38;
                                font-weight: bold;
                                margin-bottom: 5px;
                            }
                        }
                        &:last-child{
                            margin-bottom: 0;
                        }
                    }
                }
            }
        }
        
    }
    @media screen and (max-width: 1000px){
        .contact-content .contact-form{
            flex-direction: column-reverse;
            margin-top: 50px;
        }
        .contact-content .contact-form .right {
            width: 100%;
            margin: 0;
        }
        .contact-content .contact-aboutus {
            margin: 0 auto 50px;
            width: 100%;
            min-width: 0;
        }
        .contact-content .contact-aboutus .contact-aboutus-phone .info{
            .number{
                font-size: 28px;
            }
        }
        .contact-title{
            padding-left: 30px;
        }
        .contact-form-box{
            .el-input, .el-select{
                width: 48%;
            }
        }

    }
    @media screen and (max-width: 768px){
        .contact-content .contact-form .right {
            margin-bottom: 100px;
        }
        .contact-form-box{
            .el-input, .el-select{
                width: 100% !important;
            }
        }
    }     
}
.google-map {
    width: 100%;
    overflow: hidden;
    body {
        margin: 0;
    }
}
.el-input {
    width: auto;
}

</style>


